.btn-close {
  position: absolute;
  top: 8px;
  right: 8px;

  display: inline-block;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;

  border: none;
  background-color: transparent;
  transition: transform $animation;

  &:hover,
  &:focus {
    transform: scale(1.15);
    cursor: pointer;
  }
}

.main-btn {
  display: inline-flex;
  width: 110px;
  height: 44px;

  font-weight: 500;
  font-size: 12px;
  line-height: calc(16 / 12);
  border-radius: 5px;
  border: 1px solid $txt-black-color;
  outline: none;
  background-color: $bg-white-color;

  align-items: center;
  justify-content: center;
  text-transform: uppercase;

  color: $txt-black-color;
  transition: color $animation, background-color $animation,
    box-shadow $animation;

  &:hover,
  &:focus {
    cursor: pointer;
    color: $txt-white-color;
    background-color: $bg-active-color;
    border: transparent;
    box-shadow: $btn-shadow;
  }

  @media screen and (min-width: 768px) {
    width: 125px;
  }

  @media screen and (min-width: 1280px) {
    width: 136px;
  }
}

.main-btn--library {
  width: 130px;
  color: $txt-white-color;
  background-color: transparent;
  border: 1px solid $txt-white-color;

  @media screen and (min-width: 768px) {
    width: 152px;
  }

  @media screen and (min-width: 1280px) {
    width: 148px;
  }
}

.btn-wrapper {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
}

.btn-wrapper--library {
  @media screen and (min-width: 768px) {
    gap: 32px;
  }

  @media screen and (min-width: 1280px) {
    gap: 16px;
  }
}
